<template>
  <div id="music">
    <div id="title">
      <h1 class="select2">MusicPlayer</h1>
      <ul id="head">
        <li class="select2">
          <el-button>播放列表</el-button>
        </li>
        <li>
          <el-button id="showInput" @click.native="showTheBtn"
            >显示输入框</el-button
          >
        </li>
        <li v-if="showBtn">
          <input
            type="text"
            id="search"
            v-model="searchListId"
            @keyup.enter="searchMusicList"
            placeholder="输入歌单的id"
          />
        </li>
        <li class="select2">
          <el-button icon="el-icon-search" @click.native="searchMusicList"
            >歌单搜索</el-button
          >
        </li>

        <li v-if="showBtn">
          <input
            type="text"
            id="search"
            v-model="search"
            placeholder="输入歌名"
            @keyup.enter="searchMusic"
          />
        </li>
        <li class="select2">
          <el-button
            id="searchMusic"
            icon="el-icon-search"
            placeholder="输入歌名"
            @click.native="searchMusic"
            >歌曲搜索</el-button
          >
        </li>
      </ul>
    </div>
    <router-view :result="result" :sendAsk="sendAsk"></router-view>
    <div id="load">
      <div>
        <span><i @click="last" class="el-icon-d-arrow-left select"></i></span>
        <span
          ><i
            :style="pauseStyle"
            @click="pause"
            class="el-icon-video-pause select"
          ></i
        ></span>
        <span
          ><i
            :style="playStyle"
            @click="playMusic"
            class="el-icon-video-play select"
          ></i
        ></span>
        <span><i @click="next" class="el-icon-d-arrow-right select"></i></span>
        <!-- //循环播放随机播放 -->
        <span
          ><i
            class="select"
            style="cursor: pointer"
            @click="changeStyle"
            :class="playBtn"
          ></i
        ></span>
      </div>
      <div class="block">
        <span class="demonstration"></span>
        <el-slider v-model="load" @change="change($event)"></el-slider>
      </div>
      <div>
        <span><i @click="changeSound" class="el-icon-bell select"></i></span>
        <span class="demonstration1"></span>
        <el-slider
          style="width: 65%; margin-right: 20px"
          v-model="sound"
        ></el-slider>
      </div>
      <audio
        ref="music"
        autoplay
        controls
        :src="url"
        style="display: none"
        @timeupdate="timeupdate"
        @ended="end"
      ></audio>
    </div>
    <div ref="bg" id="bgImg"></div>
    <div ref="bg2" id="bgImg2"></div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      showBtn: false,
      search: "",
      load: 0,
      sound: 100,
      result: [],
      isPlay: false,
      urlArray: [],
      sendAsk: false,
      searchListId: "",
      //样式切换
      pauseStyle: {},
      playStyle: {},
      playBtn: "el-icon-s-unfold",
      plyBtnNum: 0,
    };
  },

  computed: {
    isSearch() {
      return this.$store.state.isSearch;
    },
    isList() {
      return this.$store.state.isList;
    },
    id() {
      return this.$store.state.id;
    },
    //音乐播放url
    url() {
      return this.$store.state.url;
    },
    // 音乐id数组
    idArray() {
      return this.$store.state.idArray;
    },
    // 当前播放音乐索引
    isPlayIndex() {
      return this.$store.state.isPlayIndex;
    },
    // 当前音乐图片URL
    imgUrl() {
      return this.$store.state.imgSrc;
    },
  },
  methods: {
    changeSound() {
      if (this.sound != 0 && this.sound != 100) {
        this.sound = 0;
      } else if (this.sound == 0) {
        this.sound = 100;
      } else {
        this.sound = 0;
      }
    },
    change(data) {
      // this.$refs.music.currentTime=value
      this.$refs.music.currentTime = (this.$refs.music.duration / 100) * data;
      console.log("进度条改变了", data);
    },
    //循环随机播放按钮
    changeStyle() {
      const array = [
        "el-icon-s-unfold",
        "el-icon-s-operation",
        "el-icon-refresh",
      ];
      this.plyBtnNum++;
      if (this.plyBtnNum >= 3) {
        this.plyBtnNum = 0;
      }
      this.playBtn = array[this.plyBtnNum];
    },
    showTheBtn() {
      this.showBtn = !this.showBtn;
    },
    // 下一首
    next() {
      // this.$store.commit("CHAGESONGINDEX", this.isPlayIndex + 1);
      this.end();
      // this.$refs.music.play();
    },
    // 上一首
    last() {
      this.$store.commit("CHAGESONGINDEX", this.isPlayIndex - 1);
      this.$refs.music.play();
    },

    // 播放完后播放下一首
    end() {
      switch (this.plyBtnNum + 1) {
        //状态1:按序列播放
        case 1:
          this.$store.commit("CHAGESONGINDEX", this.isPlayIndex + 1);
          break;
        //状态2:随机播放
        case 2:
          this.$store.commit(
            "CHAGESONGINDEX",
            Math.floor(Math.random() * this.idArray.length)
          );
          break;
        // 状态3:循环播放
        case 3:
          this.$store.commit("CHAGESONGINDEX", this.isPlayIndex);
          this.$refs.music.src = "";
          this.$refs.music.src = this.url;
          break;
      }
      // this.timer = setInterval(() => {
      // setTimeout(() => {
      //   this.$refs.music.play();
      // }, 1000);
      // }, 1000);
    },

    // 进度条函数
    timeupdate() {
      this.load =
        (this.$refs.music.currentTime * 100) / this.$refs.music.duration;
    },

    // 暂停音乐
    pause() {
      this.$refs.music.pause();
      this.isPlay = false;
      this.playStyle = {
        color: "rgba(225, 225, 225, 0.6)",
      };
      this.pauseStyle = { color: "white", transform: "scale(1.1)" };
    },
    // 继续音乐
    playMusic() {
      this.$refs.music.play();
      this.isPlay = true;
      this.playStyle = { color: "white", transform: "scale(1.1)" };
      this.pauseStyle = {
        color: "rgba(225, 225, 225, 0.6)",
      };
    },
    // 搜索歌单
    searchMusicList() {
      //清空数组
      this.urlArray.length = 0;
      this.result.length = 0;
      this.idArray.length = 0;
      if (this.searchListId == "" || this.searchListId == null) {
        this.searchListId = "2695791551";
      }
      this.sendAsk = true;
      this.$store.commit("CHANGEISLIST", true);
      this.$store.commit("CHANGEISSEARCH", false);
      axios
        .get(
          `https://api.yimian.xyz/msc/?type=playlist&id=${this.searchListId}`
        )
        .then(
          (response) => {
            console.log("发送成功");
            console.log(response.data, typeof response.data);

            // let array = [];
            // let array2 = [];
            for (let index = 0; index < response.data.length; index++) {
              const element = response.data[index];
              this.result.push(element);
              this.idArray.push(element.id);
              this.urlArray.push(element.url);
            }
            // this.result = array2;
            // this.$store.commit("CHANGEIDARRAY", array);
          },
          (error) => {
            console.log(error.data);
          }
        );
    },
    // 搜索音乐
    searchMusic() {
      this.idArray.length = 0;
      this.$store.commit("CHANGEISLIST", false);
      this.$store.commit("CHANGEISSEARCH", true);
      // 向子组件发送请求
      this.sendAsk = true;
      // http:cloud-music.pl-fe.cn/search?keywords=${this.search}
      // https请求
      // https://music-api.heheda.top/search?keywords=${this.search}
      axios
        .get(`https://music-api.heheda.top/search?keywords=${this.search}`)
        .then(
          (response) => {
            console.log("发送成功");
            console.log(response.data);
            this.result = response.data.result.songs;
            // let array = [];
            for (
              let index = 0;
              index < response.data.result.songs.length;
              index++
            ) {
              const element = response.data.result.songs[index];
              // this.idArray[index] = element.id;
              // array[index] = element.id;
              this.idArray.push(element.id);
            }
            // this.$store.commit("CHANGEIDARRAY", array);
            // console.log(this.result);
          },
          (error) => {
            console.log(error.data);
          }
        );
    },
  },
  beforeDestroy() {
    // this.timer = null;
    //清除定时器
    clearTimeout(this.timer);
  },
  beforeRouteEnter(to, from, next) {
    // 注意，在路由进入之前，组件实例还未渲染，所以无法获取this实例，只能通过vm来访问组件实例
    next((vm) => {
      vm.$store.commit("CHANGEISLIST", true);
      vm.$store.commit("CHANGEISSEARCH", false);
      axios.get(`https://api.yimian.xyz/msc/?type=playlist&id=2695791551`).then(
        (response) => {
          console.log("发送成功");
          console.log(response.data, typeof response.data);
          // let array = [];
          // let array2 = [];
          for (let index = 0; index < response.data.length; index++) {
            const element = response.data[index];
            // array2[index] = element;
            // array[index] = element.id;
            vm.result.push(element);
            vm.idArray.push(element.id);
            vm.urlArray.push(element.url);
          }
          // vm.result = array2;
          // vm.$store.commit("CHANGEIDARRAY", array);
        },
        (error) => {
          console.log(error.data);
        }
      );
    });
  },
  mounted() {
    // this.timer = setInterval(() => {
    //   if (this.isPlay) {
    //     this.timeupdate();
    //   }
    // }, 1000);
    this.$bus.$on("ask", (data) => {
      this.sendAsk = !this.sendAsk;
      this.urlArray = data;
    });

    //音量调节
    document.onkeydown = (event) => {
      switch (event.keyCode) {
        case 38:
          this.sound += 5;
          break;
        case 40:
          this.sound -= 5;
          break;
        default:
          break;
      }
    };

    //监听键盘,快捷键设置
    //→下一首 ←上一首 ↑增大音量 ↓减小音量 空格切换播放暂停 q切换播放模式(顺序,随机,循环)
    document.onkeyup = (event) => {
      console.log("按下:" + event.key + "键:" + event.keyCode);
      switch (event.keyCode) {
        case 39:
          this.next();
          this.isPlay = true;
          break;
        case 37:
          this.last();
          break;
        case 32:
          if (this.isPlay) {
            // this.$refs.music.pause();
            // this.isPlay = !this.isPlay;
            this.pause();
          } else {
            // this.$refs.music.play();
            // this.isPlay = !this.isPlay;
            this.playMusic();
          }
          break;
        case 81:
          this.changeStyle();
          break;
        default:
          break;
      }
    };

    // this.$store.commit("CHANGEISLIST", true);
    // this.$store.commit("CHANGEISSEARCH", false);
    // axios.get(`https://api.yimian.xyz/msc/?type=playlist&id=2695791551`).then(
    //   (response) => {
    //     console.log("发送成功");
    //     console.log(response.data, typeof response.data);

    //     let array = [];
    //     let array2 = [];
    //     for (let index = 0; index < response.data.length; index++) {
    //       const element = response.data[index];
    //       array2[index] = element;
    //       array[index] = element.id;
    //     }
    //     this.result = array2;
    //     this.$store.commit("CHANGEIDARRAY", array);
    //   },
    //   (error) => {
    //     console.log(error.data);
    //   }
    // );

    // 获取歌词(无翻译)
    // http://music.163.com/api/song/media?id=535990
    // https://music.163.com/api/song/lyric?id=535990&lv=1&kv=1&tv=-1
    // tv=-1中文翻译

    // 获取歌单信息
    // https://api.yimian.xyz/msc/?type=playlist&id=451520504
    //获取用户信息
    // https://tenapi.cn/mywyy/?uid=
    //搜索歌曲
    // http://cloud-music.pl-fe.cn/search?keywords=
    // 获取歌单
    // https://music.163.com/api/playlist/detail?id=2695791551
    //获取歌曲
    // https://music.cyrilstudio.top/song/url?id=11975048
    //获取用户
    // https://music.163.com/api/v1/user/detail/1583080072
    // axios.get("https://music.163.com/api/playlist/detail?id=2695791551").then(
    //   (response) => {
    //     console.log("发送成功", response.data);
    //   },
    //   (error) => {
    //     console.log("发送失败" + error.data);
    //   }
    // );
  },
  watch: {
    sound() {
      let audioPlayer = this.$refs.music;
      audioPlayer.volume = this.sound / 100;
    },
    //监听索引发生变化
    // `https://music.163.com/api/song/lyric?id=${this.id}&lv=1&kv=1&tv=-1`
    isPlayIndex() {
      // axios
      //   .get(
      //     // `https://music.163.com/api/song/lyric?id=${
      //     //   this.idArray[this.isPlayIndex]
      //     // }&lv=1&kv=1&tv=-1`
      //     ` https://api.paugram.com/netease/?id=517567145&play=true`
      //   )
      //   .then(
      //     (response) => {
      //       console.log("歌词为", response.data);
      //     },
      //     (error) => {
      //       console.log("发送歌词失败");
      //     }
      //   );
      console.log("当前播放歌曲发生变化了");
      // axios
      //   .get(
      //     `https://music.cyrilstudio.top/song/url?id=${
      //       this.idArray[this.isPlayIndex]
      //     }`
      //   )
      //   .then((response) => {
      //     console.log(response.data);
      //     // 改变歌曲url
      //     this.$store.commit("CHANGEURL", response.data.data[0].url);
      //     (error) => {
      //       console.log("获取url失败", error);
      //     };
      //   });
      if (this.isList) {
        this.$store.commit("CHANGEURL", this.urlArray[this.isPlayIndex]);
      } else {
        axios
          .get(
            `https://music.cyrilstudio.top/song/url?id=${
              this.idArray[this.isPlayIndex]
            }`
          )
          .then((response) => {
            console.log(response.data);
            // 改变歌曲url
            this.$store.commit("CHANGEURL", response.data.data[0].url);
            (error) => {
              console.log("获取url失败", error);
            };
          });
      }

      //歌曲url变化
      // this.$store.commit("CHANGEURL", this.urlArray[this.isPlayIndex]);
      // 歌曲id变化
      this.$store.commit("CHANGEID", this.idArray[this.isPlayIndex]);
      //图片url变化
      if (this.isSearch) {
        // 搜索歌名获取的结果
        this.$store.commit(
          "CHANGEIMGURL",
          this.result[this.isPlayIndex].al.picUrl
        );
      } else {
        // 搜索歌单获取的结果
        this.$store.commit("CHANGEIMGURL", this.result[this.isPlayIndex].cover);
      }

      //切换背景图片，增加模糊滤镜
      this.$nextTick(() => {
        this.$refs.bg.style =
          "background-image:url(" + this.imgUrl + ");filter: blur(15px);";
      });
    },
    //监听图片地址发生变化
    imgUrl() {
      this.$nextTick(() => {
        this.$refs.bg.style =
          "background-image:url(" + this.imgUrl + ");filter: blur(15px);";
      });
    },
  },
};
</script>

<style scoped>
/* 模糊滤镜底板 */
#bgImg2 {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.2);
}
/* 背景图片 */
#bgImg {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  z-index: -2;
  background-color: rgba(255, 255, 255, 0.4);
  background: linear-gradient(to top right, rgb(0, 0, 0), rgb(121, 110, 120));
  background-size: 100%;
  background-repeat: no-repeat;
  /* 背景图片左移20%下移20% */
  background-position: -20% 20%;
}
#music {
  position: relative;
  height: 100vh;
  width: 100%;
  /* background: linear-gradient(to top right, rgb(0, 0, 0), rgb(121, 110, 120)); */
  overflow: hidden;
}

.isSelect {
  color: white;
  transform: scale(1.1);
}
.noSelect {
  color: rgba(225, 225, 225, 0.6);
}
#search {
  height: 35px !important;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(255, 255, 255, 0.4);
  box-shadow: 0px 0px 5px 2px grey;
  border-radius: 2px;
  color: white;
}
i {
  font-size: 30px;
}

.select:hover {
  color: rgba(255, 255, 255, 1);
  transform: scale(1.1);
  cursor: pointer;
}
.select2:hover {
  color: white;
}
#head .el-button:hover {
  background-color: grey;
}
#load div:nth-child(1),
div:nth-child(3) {
  height: 40px;
  width: 20%;
  color: rgba(225, 225, 225, 0.6);
  display: flex;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0);
}

#load div:nth-child(2) {
  height: 20px;
  flex: 1;
  margin-right: 20px;
}
#load div:nth-child(1) {
  margin-right: 20px;
}

#load {
  position: absolute;
  bottom: 3%;
  left: 10%;
  width: 80%;
  display: flex;
  justify-content: space-between;
}
.el-button {
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(255, 255, 255, 0.4);
  box-shadow: 0px 0px 5px 2px grey;
  border-radius: 2px;
  color: white;
}

#title {
  margin-top: 50px;
  color: rgba(225, 225, 225, 0.8);
  font-family: "思源宋体 CN";
  width: 100%;
  margin-left: 50px;
}

#head {
  margin-left: 10%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 30%;
}

#head li {
  /* margin-left: 15%; */
  margin-right: 20px;
  flex: 1;
}
#head li:nth-child(3) {
  flex: 3;
}
@media screen and (max-width: 1000px) {
  #load div:nth-child(1) {
    width: 30%;
  }
}
@media screen and (max-width: 600px) {
  #load div:nth-child(1) {
    margin-right: 40px;
  }
  #load div:nth-child(3) {
    width: 25%;
  }
  #title {
    margin-left: 0px;
  }
  #head {
    flex-wrap: wrap;
    width: 100%;
  }
  #head ul {
    margin-left: 0%;
  }
}
</style>